import React from "react";
import ReactDOM from "react-dom";
import Landpage from "./components/LandPage/LandPage";
import { Route, HashRouter } from "react-router-dom";
import { Provider } from "react-redux";
import { createStore, applyMiddleware } from "redux";
import Quote from "./components/quotepage/QuotePage";
import mystore from "./Reducers/Store";
import logger from "redux-logger";
import Thunk from "redux-thunk";
import reduxprom from "redux-promise-middleware";
import Navbar from "./components/LandPage/Navbar";
const store = createStore(mystore, applyMiddleware(reduxprom, Thunk));

const App = () => {
  return (
    <Provider store={store}>
      <HashRouter basename="/quotime">
        <div
          className="container-fluid"
          style={{ height: "100vh", overflow: "hidden" }}
        >
          <div className="row">
            <Navbar className="shadow col-lg-1 col-md-1 col-sm-2 d-flex flex-column justify-content-center justify-items-center " />
            <Route path="/" exact component={Landpage} />
            <Route path="/quotes" component={Quote} />
          </div>
        </div>
      </HashRouter>
    </Provider>
  );
};

ReactDOM.render(<App />, document.querySelector("#root"));
